import { createApp } from 'vue'
import App from './App.vue'
const axios = require('axios').default;

createApp(App).mount('#app')

import Procedural from 'procedural-gl';

const container = document.getElementById('container');

// Configure datasources
const datasource = {
    provider: 'maptiler',
    // To get a free key, use https://cloud.maptiler.com/account/?ref=procedural
    apiKey: '22lJ36Yhh3RkOmtapciO'
};

// Initialize library and optionally add UI controls
Procedural.init({ container, datasource });
Procedural.setCameraMode('2D');
Procedural.setCompassVisible(false);

Procedural.setDisplayErrors(true);

// Load real-world location
const singapore = { latitude: 1.36089, longitude: 103.819, distance: 48000 };
Procedural.displayLocation(singapore);

var markers = require('./markers.json');

var all_values = {
    "central": [0, 1, 2, 3, 4, 5, 6, 7, 8],
    "north": [0, 1, 2, 3, 4, 5, 6, 7, 8],
    "south": [0, 1, 2, 3, 4, 5, 6, 7, 8],
    "east": [0, 1, 2, 3, 4, 5, 6, 7, 8],
    "west": [0, 1, 2, 3, 4, 5, 6, 7, 8],
    "national": [0, 1, 2, 3, 4, 5, 6, 7, 8]
};

Procedural.addOverlay(markers);

Procedural.onFeatureClicked = function (id) {
    document.getElementById('o3_sub_index').innerHTML = all_values[id][0]
    document.getElementById('pm10_twenty_four_hourly').innerHTML = all_values[id][1]
    document.getElementById('pm10_sub_index').innerHTML = all_values[id][2]
    document.getElementById('co_sub_index').innerHTML = all_values[id][3]
    document.getElementById('pm25_twenty_four_hourly').innerHTML = all_values[id][4]
    document.getElementById('so2_sub_index').innerHTML = all_values[id][5]
    document.getElementById('co_eight_hour_max').innerHTML = all_values[id][6]
    document.getElementById('no2_one_hour_max').innerHTML = all_values[id][7]
    document.getElementById('so2_twenty_four_hourly').innerHTML = all_values[id][8]
    document.getElementById('pm25_sub_index').innerHTML = all_values[id][9]
    document.getElementById('psi_twenty_four_hourly').innerHTML = all_values[id][10]
    document.getElementById('o3_eight_hour_max').innerHTML = all_values[id][11]
};

axios.get('https://api.data.gov.sg/v1/environment/psi').then(response => (ConsumeData(response)));
setInterval(() => {
    axios.get('https://api.data.gov.sg/v1/environment/psi').then(response => (all_values = response.data.items));
}, 300000)

function ConsumeData(response) {
    var values = response.data.items[0].readings;

    var i = 0;
    for (var row in values) {
        console.log("document.getElementById('" + row +"').innerHtml = all_values[id]["+i+"]");
        for (var region in values[row])
        {
            all_values[region][i] = values[row][region];
        }
        i++;
    }
}